.contactUs {
    text-align: center;
    h2 {
        text-align: center;
        font-size: em(40);
    }
    p {
        width: 50%;
        margin: 0 auto;
        margin-bottom: em(50);
    }
    .emailContainer {
        background: $darkerWhite;
        padding: em(30) 0;
        h2 {
            margin-top: 0;
        }
    }
    .emailButton {
        background: $titleGreen;
        color: #fff;
        border: 3px solid $titleGreen;
        font-size: em(70);
        text-decoration: none;
        width: span(2 of 12);
        margin: 0 auto;
        text-align: center;
        padding: em(3) 0;
        border-radius: 10px;
        transition: all ease-in-out 0.5s;
        &:hover {
            background: none;
            color: $titleGreen;
        }
    }
    .socialNetworks {
    	margin: em(40) 0;
    	padding: em(20) 0;
        li {
            display: inline-block;
            text-align: justify;
            margin: 0 3vw;
            color: white;
            a {
                font-size: em(50);
                color: inherit;
                i {
                    &::before {
                        height: em(50);
                        width: 100%;
                        padding: em(3) em(4);
                        background: $titleGreen;
                        border: 3px solid $titleGreen;
                        border-radius: 10px;
                        transition: all ease-in-out 0.5s;
                    }
                    &:hover::before {
                        color: $titleGreen;
                        background: none;
                    }
                }
            }
        }
    }
}
