body{
	@include container();
}
.subTitle {
    font-weight: 200;
    font-size: em(55);
    span {
        font-weight: 600;
        color: $titleGreen;
        text-transform: uppercase;
    }
}
.subSubTitle{
	@extend .subTitle;
	font-size: em(30);
}
.button{
    border: 1px solid;
    padding: em(10) em(20);
    background:  none;
}
.stopScrolling{
    overflow: hidden;
}
.contentWrapper{
    background: #fff;
}