@import '_nav.scss';
@import '_overlay.scss';
.hero {
    height: 100vh;
    // background: url('https://download.unsplash.com/13/unsplash_523b2af0710a7_1.JPG') no-repeat;
    // background-size:;
    background-attachment: fixed;
    // padding: 1px;
    @include container;
    overflow: hidden;
    h1 {
        color: white;
        position: relative;
        text-align: center;
        top: 30%;
        font-weight: 200;
        width: 50%;
        margin: 0 auto;
        span {
            text-transform: uppercase;
            font-weight: 700;
            display: block;
        }
    }
    .navBar {
        position: absolute;
        bottom: 10%;
        left: 0;
        right: 0;
        .logo {
            display: inline-block;
            @include span(3 of 12);
            margin-left: span(1 of 12);
        }
        .navButton {
            @include span(1 of 12 last)
        }
    }
}
.background{
	position:fixed;
	z-index: -1;
	background: url('https://download.unsplash.com/13/unsplash_523b2af0710a7_1.JPG');
	background-size: cover;
	height: 100vh;
	width: 100%;
}