.navButton {
        height: 100px;
        position: absolute;
        @include span (1 of 12 last);
        right: span(1 of 12 last);
        background: none;
        border: none;
        overflow: hidden;
        color: white;
        font-size: 0;
        padding: 0;
        text-indent: -9999px;
        box-shadow: none;
        cursor: pointer;
        z-index: 20000;
        &:focus {
            outline: none;
        }
        span {
            display: block;
            position: absolute;
            top: 45px;
            left: 36px;
            right: 0;
            transition: background ease-in 0.3s;
            height: 3px;
            background: #fff;
            &::before, &::after {
                position: absolute;
                display: block;
                left: 0;
                width: 100%;
                height: 3px;
                background: #fff;
                content: "";
            }
            &::before {
                top: -27px;
            }
            &::after {
                bottom: -27px;
            }
        }
    }

.navX {
    span {
        transition: background ease-out 0 0.5s;
        &::after, &::before {
            transition-duration: 0.5s, 0.5s;
            transition-delay: 1.4s, 0;
        }
        &::before {
            transition-property: top, transform;
        }
        &::after {
            transition-property: bottom, transform;
        }
    }
    &.active {
        span {
            background: none;
            &::before {
                top: 0;
                transform: rotate(45deg);
            }
            &::after {
                bottom: 0;
                transform: rotate(-45deg);
            }
            &::before,
            &::after {
                transition-delay: 0, 0.5s;
            }
        }
    }
}